* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	appearance: none;
}
$black: #000000;
$darkgrey: #303030;
$white: #ffffff;


@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

@font-face {
	font-family: 'Icon';
	src: 
		local('fonts/icon'), url('../media/fonts/icon/icon.ttf'),
		local('fonts/icon'), url('../media/fonts/icon/icon.woff2');
	font-display: block;
}
* {
	color: $darkgrey;
}
* {
	font-family: 'Inter', sans-serif;
	font-size: 10;
}

.icon {
  display: inline-block;
  font: normal normal normal 1em/1 'Icon';
  color: inherit;
  flex-shrink: 0;
  speak: none;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

$icons: (
  arrow: 'ea02',
  moon: 'ea03',
  sun: 'ea04',
  reload: 'ea05',
);

@function unicode($str) {
  @return unquote("\"\\#{$str}\"");
}

@each $name, $code in $icons {
  .icon--#{"" +$name}::before {
    content: unicode($code);
  }
}
.page {
	background-image: url('/media/images/background-day.webp');
	width: 100vw;
	min-height: 100vh;
	background-size: cover;
	background-position: center;
}